import React, { useState } from "react";
import "./navigation.css";
import { Route, HashRouter, Link, NavLink } from "react-router-dom";
import Home from "../home/home";
import About from "../about/about";
import Contact from "../contact/contact";
import AnchorLink from "react-anchor-link-smooth-scroll";
import ReorderIcon from "@material-ui/icons/Reorder";

const Navigation = () => {
  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };

  const [showLinks, setShowLinks] = useState(false);

  const scrollToComponent = (id) => {
    window.location.hash = "#" + id;
  };

  window.onscroll = function () {
    scrollFunction();
  };

  function scrollFunction() {
    if (
      document.body.scrollTop > 100 ||
      document.documentElement.scrollTop > 100
    ) {
      // document.getElementById("navbar").style.fontSize = "20px";
      document.getElementById("navbar").style.backgroundColor = "white";
      document.getElementById("navbar").style.transition = "0.5s";
    } else {
      // document.getElementById("navbar").style.fontSize = "20 px";
      document.getElementById("navbar").style.backgroundColor = "transparent";
    }
  }

  return (
    <div id="navbar" className="navigation">
      <div className="rightside"></div>
      <div className="leftside">
        <div className="links" id={showLinks ? "hidden" : ""}>
          <NavLink
            exact
            to={"portfolio"}
            onClick={() => scrollToComponent("portfolio")}
          >
            <a>Portfolio</a>
          </NavLink>

          <NavLink
            activeClassName="active"
            exact
            to={"about"}
            onClick={() => scrollToComponent("about")}
          >
            <a>About</a>
          </NavLink>

          <NavLink
            to={"contact"}
            exact
            activeClassName="active"
            onClick={() => scrollToComponent("contact")}
          >
            <a>Contact</a>
          </NavLink>
        </div>
        <button onClick={() => setShowLinks(!showLinks)} className="butt">
          {" "}
          {""} <ReorderIcon />{" "}
        </button>
      </div>
    </div>
  );
};
export default Navigation;
